import {applyEffectHandler , reduceEffectDuration , reduceEffectUses} from "./effectsHandler.js"
import pubsub from "../pubsub.js"

export class basicBuff{
static generatedEffect = "buffs"

  constructor(name,duration , uses){
    this.name = name

    this.duration = duration ?? "inf"
    this.uses = uses ?? "ind"

    this.hostingEntityHasIt = false
  }
 
  apply(entity){
    this.init(entity)
  }
  remove(){
    this.defaultRemove()
  }

  defaultInit(entity){
// for when first applied
    applyEffectHandler(this,entity)
  }
  effect(){
// subs to pubsub event and triggers
  }
  defaultRemove(){
// for removal
    delete this.hostingEntity.debuffs[this.name]
    delete this
  }

}



